@import "https://fonts.googleapis.com/icon?family=Material+Icons";

.captcha {
  padding: 15px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fafafa;
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, .3);
          box-shadow: 0 3px 6px rgba(0, 0, 0, .3);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.hidden {
  display: none;
  display: block;
}

.captcha .cols {
  -ms-flex-positive: 0;
      flex-grow: 0;
  white-space: nowrap;
  margin: 0 20px;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.captcha .cols .logo img {
  width: 60px;
}

.captcha .cols .name {
  color: #ababab;
}

.captcha .cols span {
  display: inline-block;
  vertical-align: middle;
}

.captcha .checkbox {
  width: 20px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 5px;
  -webkit-box-shadow: inset 0px 0px 1px 0px #0000003b;
          box-shadow: inset 0px 0px 1px 0px #0000003b;
  cursor: pointer;
}



